
/* 사이드바 스타일 */
.chatsidebar-container {
  display: flex;
  height: calc(100vh - 20px);
  overflow-y: auto;
  background-color: #ffffff;
  border: none;
  
  
  
  width: 1920px; /* 가로 최대 너비를 1960px로 지정 */
  margin: 0 auto; /* 가운데 정렬을 위해 좌우 여백을 자동으로 조정 */
}

.loading-text {
  margin-left: 15px;
}

.chatsidebar {
  width: 300px;
  background-color: #f4f4f4;
  padding: 10px;
  border-top-right-radius: 15px;
}

/* 채팅 헤더*/
.chat-heading {
  margin-left: 15px;
}


.chat-room-item {
  background-color: transparent;
}

.chat-room-name {
  font-size: 18px;
}

/* 채팅방 */
.chat-room-button {
  width: 100%;
  background-color: transparent;
  border: none;
  padding: 15px;
  cursor: pointer;
  text-align: left;
}

/* 채팅방 선택시 */
.selected {
  background-color: #e6e6e6;
  border-radius: 10px;
}


.chat-room-list {
  list-style-type: none;
  padding: 0;
}

/* 채팅 창 스타일 */
.chat-window {
  width: 1200px; /* 가로 최대 너비를 1960px로 지정 */
  flex-grow: 1;
  background-color: #ffffff;
  border: 1px solid #ffffff;
  margin-left: 0px;
  padding: 20px;
  overflow-y: auto;
  margin: 0 auto; /* 가운데 정렬을 위해 좌우 여백을 자동으로 조정 */
  
}

.chat-open {
  width: calc(100% - 320px);
  /* 사이드바 너비 + 채팅창과의 간격 */
}

/* 메시지 입력 폼 스타일 */
.message-input-form {
  display: flex;
}

.message-input-form input {
  flex-grow: 1;
  padding: 10px;
  font-size: 16px;
}

.message-input-form button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-left: 10px;
}

.message-input-form button:hover {
  background-color: #0056b3;
}
